/**
 * webkit scrollbar
 */
::-webkit-scrollbar-track{
    border-radius: 0;
    background-color: #e0e0e0;
}

::-webkit-scrollbar{
    width: 8px;
    height: 8px;
    background-color: #e0e0e0;
}

::-webkit-scrollbar-thumb{
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.2);

    &:hover{
        border-radius: 5px;
        background-color: rgba(0, 0, 0, 0.3);
    }

    &:vertical{}
    &:horizontal{}
}

/**
 * NProgress reset
 */
#nprogress .bar{
    background: $sub-color;
}
